import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, App, AlertController } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { SoapService } from '../soap.service';
import { api_base_url, api_user, api_pass, sc_code } from '../../config';
/**
 * Generated class for the MenuP2bPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-menu-p2b',
  providers: [SoapService],
  templateUrl: 'menu-p2b.html',
})
export class MenuP2bPage {  
  userdata:any;  
  badges:any;  

  p2buser:any;

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public app:App  ,
    public storage: Storage,
    public soapService:SoapService,
    public alertCtrl: AlertController
  ) {
    this.p2buser = navParams.get('p2buser');
    this.badges = navParams.get('badges');    
  }

  ionViewDidEnter() {
    this.storage.get('userdata').then((val) => {
      this.userdata = val;      
    });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad MenuP2bPage');
  }

  goToPage(page) {    

    this.viewCtrl.dismiss().then(() => {
      this.app.getRootNav().push(page, {
        p2buser: this.p2buser,         
      });
    });   
  }  

  isEmptyObject(obj) {
    for(var prop in obj) {
       if (obj.hasOwnProperty(prop)) {
          return false;
       }
    }  
    return true;
  }

  parse(val) {
    var intValue = parseInt(val);

    if(intValue > 0) {
        return true;
    } else {
        return false;
    }
  }

}



